@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: 'Roboto Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #454545;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1{
  margin: 10px 0 10px 0;
  text-shadow: 3px 3px #002e01;
}

.photoContainer{
  width: 80%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

h2{
  margin: 0 0 10px 0;
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    /*overflow-x: hidden;*/
}

.options{
    display: flex;
    flex-direction: row;
}

button {
    all: unset;
    background-color: #58a36c;
    padding: 10px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

input {
    all: unset;
    background-color: #FFFFFF;
    font-size: 20px;
    padding: 6px;
    border: 3px solid #58a36c;
    color: black;
}

.icon{
    height: 20px;
}

@media (max-width: 470px) {
    h1{
        font-size: 25px;
    }

    h2{
        font-size: 20px;
    }

    input{
        font-size: 16px;
    }
}

@media (max-width: 370px) {
    h1{
        font-size: 20px;
    }

    h2{
        font-size: 15px;
    }

    input{
        font-size: 12px;
        padding: 2px;
    }

    .icon{
        height: 12px;
    }
}

.photo{
    border: 4px solid white;
    margin: 10px;
    max-width: 100%;
    height: auto;
}

